.hidden {
  display: none;
}

.loader {
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
  font-weight: 400;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  height: 4px;
